import $ from 'jquery';
function instellingVeranderd() {
    $('.instellingen-bericht').removeClass('d-none');
}
function instellingOpslaan(ev) {
    if (ev.target.checkValidity()) {
        var input_1 = $(ev.target);
        var href = input_1.data('href');
        input_1.addClass('loading');
        $.ajax({
            data: {
                waarde: input_1.val(),
            },
            method: 'POST',
            url: href,
        }).then(function () {
            instellingVeranderd();
            input_1.removeClass('loading');
        });
    }
}
$(function () {
    $('.instellingKnop').on('click', instellingVeranderd);
    $('.change-opslaan').on('change', function (ev) { return instellingOpslaan(ev); });
});
